import React from "react";
import PropTypes from "prop-types";
import { makeStyles, styled } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
// import ProductFilter from "./ProductFilter";
import { useHistory } from "react-router-dom";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ProductList from "./ProductList";
import { useState, useEffect } from "react";
import axios from "axios";
import routes from "../../routes/routes";
import BaseUrl from "../constant";
// import { read, utils, writeFileXLSX } from 'xlsx';
// import * as XLSX from 'xlsx/xlsx.mjs';
import * as XLSX from "xlsx";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Modal } from "@material-ui/core";
import DeleteProducts from "./DeleteProducts";

const Input = styled("input")({
  display: "none",
});

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "80%",
  height: "80vh",
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

const notify = () => {
  console.log("toast");
  toast.success("Uploaded Successfully!", {
    position: "top-right",
    autoClose: 5000,
    hideProgressBar: true,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
  });
};

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    "aria-controls": `scrollable-auto-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: "97%",
    paddingLeft: "3%",
    backgroundColor: theme.palette.background.paper,
  },

  NestedRoot: {
    width: "100%",
    paddingLeft: "3%",
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function ScrollableTabsButtonAuto(props) {
  const [value, setValue] = React.useState(0);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [items, setItems] = useState([]);
  const [isEdit, setisEdit] = useState(false);
  const [isAdd, setIsAdd] = useState(false);



  
  const [fileState, setFileState] = React.useState({
    file: null,
    base64: null,
    objectUrl: null,
  });
  const [modalOpen, setModalOpen] = React.useState(false);

  const classes = useStyles();
  const history = useHistory();

  const handleModalOpen = () => {
    setModalOpen(true);
    handleCloseMenuButton();
  };
  const handleModalClose = () => setModalOpen(false);

  const handleClickMenuButton = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleCloseMenuButton = () => {
    setAnchorEl(null);
  };

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const CreateOrderHandleChange = () => {
    history.push(`/dashbord/product/createProduct`);
  };

  const readUploadFile = (e) => {
    e.preventDefault();
    if (e.target.files) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const data = e.target.result;
        console.log("data", data);
        const workbook = XLSX.read(data, { type: "array" });
        console.log("workbook", workbook);
        const sheetName = workbook.SheetNames[0];
        console.log("sheetName", sheetName);
        const worksheet = workbook.Sheets[sheetName];
        console.log("worksheet", worksheet);
        const json = XLSX.utils.sheet_to_json(worksheet);
        console.log("json", json);
      };
      reader.readAsArrayBuffer(e.target.files[0]);
    }
  };

  const readExcel = (file) => {
    const promise = new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsArrayBuffer(file);
      fileReader.onload = (e) => {
        const bufferArray = e.target.result;
        const wb = XLSX.read(bufferArray, { type: "buffer" });
        const wsname = wb.SheetNames[0];
        const ws = wb.Sheets[wsname];
        const data = XLSX.utils.sheet_to_json(ws);
        console.log("data", data);
        resolve(data);
      };
      fileReader.onerror = (error) => {
        reject(error);
      };
    });

    promise.then((d) => {
      setItems(d);
      // console.log("d", d);
      axios({
        method: "post",
        url: routes.api.product.bulkUpload.url,
        data: "",
      }).then((response) => {
        if (response) {
          if (response.data.success) {
            // enqueueSnackbar("Added Successfully", { variant: "success" });
            // notify();
            // setSubmitting(false);
            // resetForm();
            // handleCloseModal();
            console.log("sent", d);
          }
        }
      });
    });
  };

  let inputFileRef = null;

  async function readDataUrl(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      // async event handlers
      reader.onload = (e) => resolve(reader.result);
      reader.onerror = (e) => reject(reader.error);
      reader.readAsDataURL(file);
    });
  }

  const handleFileUpload = (e) => {
    const target = e.currentTarget;
    const file = target.files.item(0);
    console.log("file", file);

    const data = new FormData();
    data.append("file", file);

    console.log("data", data);

    // validate file as image
    // if (!file.type.startsWith("image/")) {
    //   alert("File is not an image");
    //   return;
    // }

    // store reference to the File object and a base64 representation of it
    readDataUrl(file).then((dataUrl) => {
      setFileState({
        ...fileState,
        file,
        base64: dataUrl,
        objectUrl: URL.createObjectURL(file),
      });
    });
    axios
      .post(BaseUrl + "bulkUpload", data, {
        // headers: {
        //   'Content-Type': 'multipart/form-data',
        // }
      })
      .then(function (response) {
        handleCloseMenuButton();
        notify();
        // console.log("response", response);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const handleClose =()=>{
    setisEdit(false)
  }
  const handleCloseAdd =()=>{
    setIsAdd(false)
  }

  const handleEditQuantity = () =>{}

  return (
    <div className={classes.root}>
      <ToastContainer />
      <header className="headerOrder">
        <div className="header__row">
          <div className="Texts_Display">Products</div>
          <div>
            <Button
              aria-controls="simple-menu"
              aria-haspopup="true"
              variant="contained"
              color="primary"
              className="buttonColor"
              onClick={handleClickMenuButton}
            >
              Action
            </Button>
            <Menu
              id="simple-menu"
              anchorEl={anchorEl}
              // keepMounted
              open={Boolean(anchorEl)}
              onClose={handleCloseMenuButton}
            >
              <MenuItem
                onClick={()=>{
                  setisEdit(true) 
                  setAnchorEl(null)}}
                className="text-theme border_bottom"
              >
                Edit
              </MenuItem>
              <MenuItem
                 onClick={()=>{
                  setIsAdd(true) 
                  setAnchorEl(null)}}
                className="text-theme border_bottom"
              >
                Add
              </MenuItem>
              <MenuItem
                onClick={handleCloseMenuButton}
                className="text-theme border_bottom"
              >
                Transfer
              </MenuItem>
              <MenuItem
                onClick={handleCloseMenuButton}
                className="text-theme border_bottom"
              >
                Download Reconcilation Worksheet
              </MenuItem>

              <MenuItem
                onClick={handleCloseMenuButton}
                className="text-theme border_bottom"
              >
                Print SKU / Barcode Label{" "}
              </MenuItem>

              <MenuItem
                onClick={handleModalOpen}
                className="text-theme border_bottom"
              >
                Delete Variant
              </MenuItem>

              <MenuItem className="text-theme">
                <div>
                  <form>
                    <label htmlFor="upload">
                      <Input
                        id="upload"
                        name="upload"
                        type="file"
                        accept="doc/*"
                        ref={(input) => (inputFileRef = input)}
                        onChange={handleFileUpload}
                        // onChange={(e) => {
                        //   const file = e.target.files[0];
                        //   // console.log("file", file);
                        //   readExcel(file);
                        //   handleCloseMenuButton();
                        // }}
                        // onChange={readUploadFile}
                      />
                      Bulk Upload
                    </label>
                  </form>
                </div>
              </MenuItem>
            </Menu>
            <Button
              variant="contained"
              color="primary"
              className="buttonColor ml-2"
              onClick={CreateOrderHandleChange}
            >
              Create New Product
            </Button>
          </div>
        </div>
      </header>
      <AppBar position="static" color="white" className="AppBarStyle">
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
          aria-label="scrollable auto tabs example"
        >
          <Tab label="All" {...a11yProps(0)} />
          <Tab label="Needs Reorder" {...a11yProps(1)} />
          <Tab label="Expected" {...a11yProps(2)} />
          <Tab label="Shipped" {...a11yProps(3)} />
          <Tab label="Returned" {...a11yProps(4)} />
          <Tab label="In Stock" {...a11yProps(5)} />
          <Tab label="Backorder" {...a11yProps(5)} />
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0} className="tabPanalOrchard">
        {/* <ProductFilter /> */}
        <ProductList isEdit={isEdit} onClose={handleClose} isAdd={isAdd} onCloseAdd={handleCloseAdd}/>
      </TabPanel>
      <TabPanel value={value} index={1}>
        {/* <ProductFilter /> */}
      </TabPanel>
      <TabPanel value={value} index={2}>
        {/* <ProductFilter /> */}
      </TabPanel>
      <TabPanel value={value} index={3}>
        {/* <ProductFilter /> */}
      </TabPanel>
      <TabPanel value={value} index={4}>
        {/* <ProductFilter /> */}
      </TabPanel>
      <TabPanel value={value} index={5}>
        {/* <ProductFilter /> */}
      </TabPanel>
      <TabPanel value={value} index={6}>
        {/* <ProductFilter /> */}
      </TabPanel>
      <Modal
        open={modalOpen}
        onClose={handleModalClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
         
            <DeleteProducts />
         
        </Box>
      </Modal>
    </div>
  );
}
