import React from "react";
import { filter } from "lodash";
import MUIDataTable from "mui-datatables";
import { Icon } from "@iconify/react";
import plusFill from "@iconify/icons-eva/plus-fill";
import { Form, FormikProvider, useFormik } from "formik";
import { sentenceCase } from "change-case";
import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import moreVerticalFill from "@iconify/icons-eva/more-vertical-fill";
import Dialog from "@material-ui/core/Dialog";
// material
// import {createMuiTheme, MuiThemeProvider} from '@material-ui/core/styles';
import {
  Box,
  Card,
  Container,
  IconButton,
  Button,
  Menu,
  MenuItem,
  TextField,
  createMuiTheme,
  MuiThemeProvider,
  useTheme,
  Grid,
  DialogTitle,
  DialogContent,
  TablePagination,
  Typography,
  Modal,
} from "@material-ui/core";
import { LoadingButton } from "@material-ui/lab";
// import { useSnackbar } from "notistack";
import { ToastContainer, toast } from 'react-toastify';
  import 'react-toastify/dist/ReactToastify.css';
import * as Yup from "yup";

import routes from "../../routes/routes";
import QRCodeDialogBox from "./QRCodeDialogBox";
// import VehicleDialogbox from './VehicleDialogBox';

// ----------------------------------------------------------------------

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

export default function SubBinsList() {
  const theme = useTheme();
  // const enqueueSnackbar = useSnackbar();
  //   const { userList } = useSelector((state) => state.users);

  const [bins, setBins] = useState([]);
  const [shelfs, setShelfs] = useState([]);
  const [racks, setRacks] = useState([]);
  const [warehouses, setWarehouses] = useState([]);
  // this state holds the subBin data created in the useEffect....
  const [subBins, setSubBins] = useState([]);
  // o stands for orignal data for the Bins.....
  const [osubBins, setOSubBins] = useState([]);
  const [editItem, setEditItem] = useState({});
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [isSelectBox, setIsSelectBox] = useState("");
  const [isQRModal, setIsQRModal] = useState(false);
  const [searchText, setSearchText] = useState("");
  const [refresh, setRefresh] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [totalCount, setTotalCount] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [page, setPage] = useState(0);
  const [modalOpen, setModalOpen] = React.useState(false);

  const handleModalOpen = () => setModalOpen(true);
  const handleModalClose = () => setModalOpen(false);

  const open = Boolean(anchorEl);

  let subBinsData = [];

  const EditClick = (event, _id, tableMeta) => {
    const data = subBins.at(tableMeta.rowIndex);
    const rowdata = osubBins.find((item) => item._id === data._id);

    setAnchorEl(event.currentTarget);
    setEditItem(rowdata);
    // console.log(email);
    setIsQRModal(true);
    handleModalOpen()
  };

  const handleChangePage = (event, newPage) => {
    console.log(newPage);
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    // console.log(parseInt(event.target.value, 10))
    setRowsPerPage(parseInt(event.target.value, 10));
    // setPage(0);
  };

  const handleCloseModal = () => {
    // console.log('check');
    setIsQRModal(false);
    setIsOpenModal(false);
    setIsSelectBox("");
    setRefresh(!refresh);
  };

  const handleModal = () => {
    // console.log('check');
    setIsOpenModal(true);
    // setRefresh(!refresh);
  };

  const handleSelect = (e) => {
    const obj = { warehouse: "racks", rack: "shelfs", shelf: "bins" };
    const { name, value } = e.target;
    // console.log(obj[name])
    let newUrl = "";
    if (name === "warehouse") {
      newUrl= routes.api.racks.getRacksByWhId.url
    }
    if (name === "rack") {
      newUrl= routes.api.shelfs.getShelfsByRackId.url
    }
    if (name === "shelf") {
      newUrl= routes.api.bins.getBinsByShelfId.url
    }
    if (name) {
      axios({
        method: "get",
        url: newUrl,
        params: { _id: value },
      }).then((response) => {
        if (response) {
          if (response.data.success) {
            setIsSelectBox(obj[name]);
            if (name === "warehouse") {
              setRacks(response.data.racks);
            }
            if (name === "rack") {
              setShelfs(response.data.shelfs);
            }
            if (name === "shelf") {
              setBins(response.data.bins);
            }
          }
        }
      });
    }
  };

  const getMuiTheme = () => createMuiTheme({});

  const columns = [
    {
      name: "w_no",
      label: "Warehouse No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `W${value}`,
      },
    },
    {
      name: "r_no",
      label: "Rack No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `R${value}`,
      },
    },
    {
      name: "s_no",
      label: "Shelf No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `S${value}`,
      },
    },
    {
      name: "b_no",
      label: "Bin No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `B${value}`,
      },
    },
    {
      name: "no",
      label: "Sub-Bins No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `SB${value}`,
      },
    },
    {
      name: "_id",
      label: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => (
          <Button
            style={{ backgroundColor: "lightgray", textAlign: "start" }}
            onClick={(e) => EditClick(e, value, tableMeta)}
          >
            QR Code
          </Button>
        ),
      },
    },
  ];

  const handleChange = (e) => {
    setSearchText(e.target.value);
  };

  // this function is handling the search feature of the datatable.
  // it's searching from the DB data of this collection.
  // we can only search with the collection data field no with the
  // fields populated fields.
  const handleCustomSearch = (e) => {
    axios({
      method: "get",
      // url: routes.api.subBins.getSearchedSubBins.url,
      params: { limit: 50, page: page, search: searchText },
    }).then((response) => {
      if (response.data.success) {
        setTotalCount(response.data.totalDoc);

        response.data.subBins.map((data) => {
          const newdata = {
            _id: data._id,
            no: data.no,
            b_no: data.bin_id.no,
            s_no: data.bin_id.shelf_id.no,
            r_no: data.bin_id.shelf_id.rack_id.no,
            w_no: data.bin_id.shelf_id.rack_id.warehouse_id.no,
          };
          subBinsData = [...subBinsData, newdata];
          return subBinsData;
        });
        setSubBins(subBinsData);
        setOSubBins(response.data.subBins);
      }
    });
  };
  
  // custom search text field and button
  const handleSearch = (value) => (
    <div
      style={{
        display: "flex",
        justifyContent: "flex-start",
        alignItems: "center",
      }}
    >
      <TextField
        // select

        label="Search by Sub-Bin"
        inputProps={{ type: "number" }}
        onChange={(e) => handleChange(e)}
        // {...getFieldProps('noOfSubBins')}
        // error={Boolean(
        //   touched.noOfSubBins && errors.noOfSubBins
        // )}
        // helperText={touched.noOfSubBins && errors.noOfSubBins}
      />
      <Button
        variant="contained"
        style={{ marginLeft: "10px" }}
        onClick={(e) => handleCustomSearch(e)}
      >
        Search
      </Button>
    </div>
  );

  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
    customSearchRender: (value) => handleSearch(value),
  };

  const getAllSubBins = async () => {
    console.log("hello");

    const wData = axios({
      method: "get",
      url: routes.api.warehouses.getAllWarehouses.url,
    });

    const sbData = axios({
      method: "get",
      url: routes.api.subBins.getAllSubBins.url,
      params: { limit: rowsPerPage, page: page },
    });

    const [subBin, warehouse] = await axios.all([sbData, wData]);

    await console.log("subbindata", subBin.data);

    if (warehouse.data.success) {
      setWarehouses(warehouse.data.warehouses);
    }

    if (subBin.data.success) {
      setTotalCount(subBin.data.totalDoc);

      subBin.data.subBins.map((data) => {
        const newdata = {
          _id: data._id,
          no: data.no,
          b_no: data.bin_id.no,
          s_no: data.bin_id.shelf_id.no,
          r_no: data.bin_id.shelf_id.rack_id.no,
          w_no: data.bin_id.shelf_id.rack_id.warehouse_id.no,
        };
        subBinsData = [...subBinsData, newdata];
        return subBinsData;
      });
      setSubBins(subBinsData);
      setOSubBins(subBin.data.subBins);
    }
  };

  useEffect(() => {
    getAllSubBins();
  }, [refresh, page, rowsPerPage]);

  const subBinSchema = Yup.object().shape({
    bin_id: Yup.string().required("Required"),
    noOfSubBins: Yup.number()
      .required("Required")
      .positive("Should be Positive")
      .integer(),
  });

  const notify = () => {
    console.log('toast');
    toast.success('Added Successfully!', {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: true,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      });
};

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      noOfSubBins: 1,
      bin_id: "",
    },
    validationSchema: subBinSchema,

    onSubmit: (values, { setErrors, setSubmitting, resetForm }) => {
      //   console.log(values);

      axios({
        method: "post",
        url: routes.api.subBins.addNewSubBins.url,
        data: values,
      }).then((response) => {
        if (response) {
          if (response.data.success) {
            // enqueueSnackbar("Added Successfully", { variant: "success" });
            notify()
            setSubmitting(false);
            resetForm();
            handleCloseModal();
          }
        }
      });
    },
  });

  const {
    values,
    errors,
    touched,
    isSubmitting,
    handleSubmit,
    getFieldProps,
    setFieldValue,
  } = formik;

  return (
    <div>
      <Container maxWidth="xl" style={{ padding: "0px" }}>
      <ToastContainer />
        <Grid style={{ display: "flex", flexDirection: "column" }}>
          <Grid
            item
            xs={12}
            sm={12}
            style={{ marginBottom: "5px", textAlign: "right" }}
          >
            <Button
              variant="contained"
              startIcon={<Icon icon={plusFill} width={20} height={20} />}
              onClick={handleModal}
              className="primaryButton"
            >
              New Sub-Bins
            </Button>
          </Grid>
          <Grid item xs={12} sm={12}>
            <Card>
              <MuiThemeProvider theme={theme} style={{ boxShadow: "none" }}>
                <MUIDataTable
                  style={{ th: { backgroundColor: "grey" } }}
                  //   title="Employees List"
                  data={subBins}
                  columns={columns}
                  options={option}
                />
                <TablePagination
                  component="div"
                  count={totalCount}
                  page={page}
                  onPageChange={handleChangePage}
                  rowsPerPage={rowsPerPage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </MuiThemeProvider>
            </Card>
          </Grid>
        </Grid>
        {isQRModal ? (
          <Modal
          open={modalOpen}
          onClose={handleModalClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
          <QRCodeDialogBox
            isQRModal={isQRModal}
            isType="bin_id"
            editItem={editItem}
            onClose={handleCloseModal}
          />
          </Box>
          </Modal>
        ) : null}
        {isOpenModal ? (
          <Dialog open={isOpenModal} onClose={handleCloseModal}>
            <DialogTitle>Add Bins</DialogTitle>
            <DialogContent>
              <FormikProvider value={formik}>
                <Form autoComplete="off">
                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={12}>
                      {isSelectBox === "bins" ? (
                        <TextField
                          select
                          fullWidth
                          label="Bins"
                          {...getFieldProps("bin_id")}
                          SelectProps={{ native: true }}
                          error={Boolean(touched.bin_id && errors.bin_id)}
                          helperText={touched.bin_id && errors.bin_id}
                        >
                          <option value="" />
                          {bins.map((option) => (
                            <option key={`${option._id}`} value={option._id}>
                              {`W${option.shelf_id.rack_id.warehouse_id.no}/
                            R${option.shelf_id.rack_id.no}/
                            S${option.shelf_id.no}/
                            B${option.no}`}
                            </option>
                          ))}
                        </TextField>
                      ) : null}
                      {isSelectBox === "shelfs" ? (
                        <TextField
                          select
                          fullWidth
                          label="Shelfs"
                          name="shelf"
                          onChange={(e) => handleSelect(e)}
                          SelectProps={{ native: true }}
                        >
                          <option value="" />
                          {shelfs.map((option) => (
                            <option key={`${option._id}`} value={option._id}>
                              {`W${option.rack_id.warehouse_id.no}/R${option.rack_id.no}/S${option.no}`}
                            </option>
                          ))}
                        </TextField>
                      ) : null}
                      {isSelectBox === "racks" ? (
                        <TextField
                          select
                          fullWidth
                          name="rack"
                          label="Racks"
                          onChange={(e) => handleSelect(e)}
                          SelectProps={{ native: true }}
                        >
                          <option value="" />
                          {racks.map((option) => (
                            <option key={`${option._id}`} value={option._id}>
                              {`W${option.warehouse_id.no}/R${option.no}`}
                            </option>
                          ))}
                        </TextField>
                      ) : null}
                      {isSelectBox === "" ? (
                        <TextField
                          select
                          fullWidth
                          name="warehouse"
                          label="Warehouse"
                          onChange={(e) => handleSelect(e)}
                          SelectProps={{ native: true }}
                        >
                          <option value="" />
                          {warehouses.map((option) => (
                            <option key={`${option._id}`} value={option._id}>
                              {`W${option.no}/${option.location}`}
                            </option>
                          ))}
                        </TextField>
                      ) : null}
                    </Grid>
                    <Grid item xs={12} sm={12}>
                      <TextField
                        // select
                        fullWidth
                        label="No of Sub-Bin"
                        inputProps={{ type: "number" }}
                        {...getFieldProps("noOfSubBins")}
                        error={Boolean(
                          touched.noOfSubBins && errors.noOfSubBins
                        )}
                        helperText={touched.noOfSubBins && errors.noOfSubBins}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <Box
                        sx={{
                          mt: 1,
                          display: "flex",
                          justifyContent: "center",
                        }}
                      >
                        <Button
                          onClick={handleCloseModal}
                          variant="contained"
                          color="secondary"
                          sx={{ mr: 1 }}
                        >
                          Cancel
                        </Button>
                        <Button
                          type="submit"
                          variant="contained"
                          pending={isSubmitting}
                          onClick={(e) => {
                            e.preventDefault();
                            handleSubmit();
                          }}
                        >
                          Save
                        </Button>
                      </Box>
                    </Grid>
                  </Grid>
                </Form>
              </FormikProvider>
            </DialogContent>
          </Dialog>
        ) : null}
      </Container>
    </div>
  );
}
