import React from "react";
import { alpha, makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import InputBase from "@material-ui/core/InputBase";
import Badge from "@material-ui/core/Badge";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import MenuIcon from "@material-ui/icons/Menu";
import SearchIcon from "@material-ui/icons/Search";
import AccountCircle from "@material-ui/icons/AccountCircle";
import MailIcon from "@material-ui/icons/Mail";
import NotificationsIcon from "@material-ui/icons/Notifications";
import MoreIcon from "@material-ui/icons/MoreVert";
import Icon from "@material-ui/core/Icon";
import SaveIcon from "@material-ui/icons/Save";
import Button from "@material-ui/core/Button";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import MutiSelect from "./MutiSelect";
import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";
import { Grid } from "@material-ui/core";
import { Card } from "@material-ui/core";
import { MuiThemeProvider, useTheme } from "@material-ui/core";
import { TablePagination } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import axios from "axios";
import routes from "../../routes/routes";
import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import LoadingOverlay from 'react-loading-overlay'
import { Link } from 'react-router-dom'


const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
}));

const ProductList = ({ isEdit, onClose, isAdd, onCloseAdd }) => {
  const history = useHistory();
  const [open, setOpen] = useState(false);
  const [warehouses, setWarehouses] = useState([]);
  const [updateProduct, setUpdateProduct] = useState([]);
  const [addProductQuantity, setAddProductQuantity] = useState([]);
  const [owarehouses, setOWarehouses] = useState([]);
  const [editItem, setEditItem] = useState({});
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [isQRModal, setIsQRModal] = useState(false);
  const [userId, setUserId] = useState("");
  const [refresh, setRefresh] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [totalCount, setTotalCount] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [page, setPage] = useState(0);
  const [modalOpen, setModalOpen] = React.useState(false);
  const [isActive, setIsActive] = useState(false);
  const [showInput, setShowInput] = useState(isEdit);
  const token = localStorage.getItem('JWT')

  console.log('token', token);

  const EditOrderHandleChange = (rowData) => {
    console.log("rowData", rowData);
    console.log("history", history);
    history.push('/dashbord/product/editProduct', rowData);
  };

  const handleChangePage = (event, newPage) => {
    console.log("newPage", newPage);
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    console.log("parseInt", parseInt(event.target.value, 10))
    setRowsPerPage(parseInt(event.target.value, 10));
    // setPage(0);
  };

  const handleModalOpen = () => setModalOpen(true);

  const EditClick = (event, _id, tableMeta) => {
    const data = warehouses.at(tableMeta.rowIndex);
    // getting the orignal item.....
    const rowdata = owarehouses.find((item) => item._id === data._id);
    console.log(rowdata);
    setAnchorEl(event.currentTarget);
    setEditItem(rowdata);
    setIsQRModal(true);
    handleModalOpen();
  };

  const HandleEditQuantity = (data, e) => {
    // console.log("data", data, e.target.value)

    const item = warehouses[data]
    const newObject = { ...item }
    newObject['quantity'] = e.target.value
    // warehouses[data] = newObject+


    let array = updateProduct

    array.push(newObject)
    console.log("array", array)
    // setWarehouses(warehouses)
    setUpdateProduct(array)

  }
  const HandleAddQuantity = (data, e) => {
    // console.log("data", data, e.target.value)
    const item = warehouses[data]
    console.log("item", item['quantity'])
    const newObject = { ...item }
    newObject['quantity'] = e.target.value
    console.log("newObject", newObject['quantity'])

    const sum = +newObject['quantity'] + +item['quantity'];

    const addedObject = { ...item }

    addedObject['quantity'] = sum;

    console.log("addedObject", addedObject)
    // warehouses[data] = newObject
    let array = addProductQuantity
    // console.log("array", array)
    array.push(addedObject)
    console.log("array", array)
    // setWarehouses(warehouses)
    setAddProductQuantity(array)
  }
  const handleClose = () => {
    onClose()
  }
  const handleClodeAdd = () => {
    onCloseAdd()
  }
  const handleUpdateProductCost = async (event) => {
    event.stopPropagation()
    await axios.put(routes.api.product.UpdateProductQuantity.url,
      {
        updateProduct
      },
    )
      .then(function (response) {
        console.log(response.data.message);
        onClose()
        setRefresh(!refresh)
      })
      .catch(function (error) {
        console.log(error);
      });
  }
  const handleAddProductQuantity = async () => {
    await axios.put(routes.api.product.AddProductQuantity.url, {
      addProductQuantity
    })
      .then(function (response) {
        console.log(response.data.message);
        // onClose()
        onCloseAdd()
        setRefresh(!refresh)
      })
      .catch(function (error) {
        console.log(error);
      });
  }
  const handleProduct = (rowData) => {
    console.log("rowData", rowData)
    history.push('/dashbord/product/editProduct', rowData); 
  }
  const columns = [
    {
      name: "_id",
      label: "id",
      options: {
        display: false,
      },
    },
    {
      name: "dataIndex",
      label: "Number",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "productName",
      label: "Product Name",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue, rowIndex) =>
        (
          <span onClick={() => { handleProduct(warehouses[tableMeta.rowIndex]) }} className="linkSpan">{value} </span>
        )
      },
    },
    {
      name: 'productCategory',
      label: 'Product Category',
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`
      }
    },
    {
      name: 'sku',
      label: 'SKU',
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`
      }
    },
    {
      name: 'barcode',
      label: 'Barcode',
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`
      }
    },
    {
      name: 'cost',
      label: 'Cost',
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`
      }
    },
    {
      name: 'quantity',
      label: 'Quantity',
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => (
          isEdit ? (
            <input type="text"
              defaultValue={value}
              // placeholder={value}
              onChange={(e) => HandleEditQuantity(tableMeta.rowIndex, e)}
              className="form-control"
            />
          )
            : isAdd ? (
              <input type="text"
                // defaultValue={value}
                placeholder={value}
                onChange={(e) => HandleAddQuantity(tableMeta.rowIndex, e)}
                className="form-control"
              />)
              : `${value}`



        )
      }
    },

  ];
  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
    // onRowClick: (rowData, rowState) => {
    //   console.log(rowData, rowState);
    //   EditOrderHandleChange(rowData);
    // },
    // selectableRows: 'single',
  };
  let productData = [];
  const getAllProducts = async () => {


    setIsActive(true)
    setRefresh(true);
    const pData = axios({
      method: "get",
      url: routes.api.product.getAllProducts.url,
      params: { limit: rowsPerPage, page: page },
      // headers: {Authorization: 'Bearer ' + token}

    });
    const [product] = await axios.all([pData]);
    console.log("productdata", product);
    if (product.data.success) {
      // setRefresh(false)
      setTotalCount(product.data.totalDoc);

      product.data.doc.map((data, index) => {
        console.log("dddddddddddddddd", data);
        const newdata = {
          _id: data._id,
          dataIndex: index + 1,
          id: data.ID,
          productName: data.ProductName,
          productCategory: data.ProductCategories,
          sku: data.SKU,
          barcode: data.Barcode,
          cost: data.CurrentSHMDefaultCost,
          quantity: data.CurrentSHMQuantity,
        };
        productData = [...productData, newdata];
        return productData;
      });
      setWarehouses(productData);
      console.log("warehouses", warehouses);
      setIsActive(false)
      // setOWarehouses(warehouses.data.warehouses);
    }
  };

  useEffect(() => {
    getAllProducts();
  }, [refresh, page, rowsPerPage]);

  return (
    <LoadingOverlay
      active={isActive}
      spinner
      text='Loading ...'
    >
    <div>
      <div className="subContainer">
        <Grid item xs={12} sm={12}>
          <Card>
            <MuiThemeProvider >
              <MUIDataTable
                style={{ th: { backgroundColor: 'grey' } }}
                //   title="Employees List"
                data={warehouses}
                columns={columns}
                options={option}
              />
              <TablePagination
                component="div"
                count={totalCount}
                page={page}
                onPageChange={handleChangePage}
                rowsPerPage={rowsPerPage}
                onRowsPerPageChange={handleChangeRowsPerPage}
              />
            </MuiThemeProvider>
          </Card>
          {isEdit ? (
            <div className="Footerdiv">
              <Button variant="contained" onClick={handleClose}>Cancel</Button>
              <Typography>You are editing Inventory</Typography>
              <Button
                variant="contained"
                color="primary"
                onClick={handleUpdateProductCost}>Save</Button>
            </div>
          )

              : isAdd ? (
                <div className="Footerdiv">
                  <Button variant="contained" onClick={handleClodeAdd}>Cancel</Button>
                  <Typography>You are editing Inventory</Typography>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handleAddProductQuantity}>Add Inventory</Button>
                </div>
              )
                : ``}
          </Grid>
        </div>
      </div>
    </LoadingOverlay>
  );
};
export default ProductList;
