import React from "react";
import { alpha, makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import InputBase from "@material-ui/core/InputBase";
import Badge from "@material-ui/core/Badge";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import MenuIcon from "@material-ui/icons/Menu";
import SearchIcon from "@material-ui/icons/Search";
import AccountCircle from "@material-ui/icons/AccountCircle";
import MailIcon from "@material-ui/icons/Mail";
import NotificationsIcon from "@material-ui/icons/Notifications";
import MoreIcon from "@material-ui/icons/MoreVert";
import Icon from "@material-ui/core/Icon";
import SaveIcon from "@material-ui/icons/Save";
import Button from "@material-ui/core/Button";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import MutiSelect from "./MutiSelect";
import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";
import { Grid } from "@material-ui/core";
import { Card } from "@material-ui/core";
import { MuiThemeProvider, useTheme } from "@material-ui/core";
import { TablePagination } from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import axios from "axios";
import routes from "../../routes/routes";
import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
}));

const DeleteProducts = (props) => {
  const [warehouses, setWarehouses] = useState([]);
  const [userId, setUserId] = useState("");
  const [refresh, setRefresh] = useState(false);
  const [totalCount, setTotalCount] = useState(0);
  const [totalCount2, setTotalCount2] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(100);
  const [rowsPerPage2, setRowsPerPage2] = useState(100);
  const [page, setPage] = useState(0);
  const [page2, setPage2] = useState(0);
  const [selectedForDelete, setSelectedForDelete] = useState([]);
 

  const handleChangePage = (event, newPage) => {
    console.log("newPage", newPage);
    setPage(newPage);
  };

  const handleChangePage2 = (event, newPage) => {
    console.log("newPage", newPage);
    setPage2(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    console.log("parseInt", parseInt(event.target.value, 10));
    setRowsPerPage(parseInt(event.target.value, 10));
    // setPage(0);
  };

  const handleChangeRowsPerPage2 = (event) => {
    console.log("parseInt", parseInt(event.target.value, 10));
    setRowsPerPage2(parseInt(event.target.value, 10));
    // setPage(0);
  };

  const handleActionAdd = (rowData) => {
    console.log("rowData", rowData);
    dummyarr = selectedForDelete;
    dummyarr.push(rowData);

    setSelectedForDelete([...dummyarr]);
    console.log("selectedForDelete", selectedForDelete);

    setTotalCount2(selectedForDelete.length);
  };

  const handleDisable = (value) => {
    const res = selectedForDelete.find((item) => item._id === value);
    return res ? true : false;
  };

  const handleActionDelete = (value) => {
    //  console.log(selectedForDelete.filter(x => x._id !== value));
    let deletedArray = selectedForDelete.filter((x) => x._id !== value);
    setSelectedForDelete(deletedArray);
  };

  // all
  const columns = [
    {
      name: "_id",
      label: "id",
      options: {
        display: false,
      },
    },
    {
      name: "productName",
      label: "Product Name",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "productCategory",
      label: "Product Category",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "sku",
      label: "SKU",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "quantity",
      label: "Quantity",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "productName",
      label: "All Available Variants",
      options: {
        filter: true,
        // sort: true,
        customBodyRender: (value, tableMeta, updateValue) => {
          console.log(tableMeta.rowData, "......");
          return (
            <div>
              {tableMeta.rowData[1]}
              <br />
              {tableMeta.rowData[2]}
              <br />
              SKU {tableMeta.rowData[3]}
              <br />
              Qty in stock: {tableMeta.rowData[4]}
            </div>
          );
        },
      },
    },
    {
      name: "_id",
      label: "Action",
      options: {
        // display: tru,
        // filter: true,
        // sort: true,
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div>
              <Button
                disabled={handleDisable(value)}
                onClick={() => handleActionAdd(warehouses[tableMeta.rowIndex])}
              >
                +
              </Button>
            </div>
          );
        },
      },
    },
  ];

  // to be deleted
  const columns2 = [
    {
      name: "_id",
      label: "id",
      options: {
        display: false,
      },
    },
    {
      name: "productName",
      label: "Product Name",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "productCategory",
      label: "Product Category",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "sku",
      label: "SKU",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "quantity",
      label: "Quantity",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "productName",
      label: "All Available Variants",
      options: {
        filter: true,
        // sort: true,
        customBodyRender: (value, tableMeta, updateValue) => {
          console.log(tableMeta.rowData, "......");
          return (
            <div>
              {tableMeta.rowData[1]}
              <br />
              {tableMeta.rowData[2]}
              <br />
              SKU {tableMeta.rowData[3]}
              <br />
              Qty in stock: {tableMeta.rowData[4]}
            </div>
          );
        },
      },
    },
    {
      name: "_id",
      label: "Action",
      options: {
        // display: tru,
        // filter: true,
        // sort: true,
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div>
              <Button onClick={() => handleActionDelete(value)}>-</Button>
            </div>
          );
        },
      },
    },
  ];

  let dummyarr = [];

  // all
  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
    print: false,
    download: false,
    viewColumns: false,
    responsive: "vertical",
    tableBodyHeight: "400px",
    tableBodyMaxHeight: "100%",
    rowsPerPage: [100],
  };

  // to be del
  const option2 = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
    print: false,
    download: false,
    viewColumns: false,
    responsive: "vertical",
    tableBodyHeight: "400px",
    tableBodyMaxHeight: "100%",
    rowsPerPage: [100],
  };

  let productData = [];

  const getAllProducts = async () => {
    setRefresh(true);
    const pData = axios({
      method: "get",
      url: routes.api.product.getAllProducts.url,
      params: { limit: rowsPerPage, page: page },
    });

    const [product] = await axios.all([pData]);

    console.log("productdata", product);

    if (product.data.success) {
      setTotalCount(product.data.totalDoc);

      product.data.doc.map((data, index) => {
        console.log("dddddddddddddddd", data);
        const newdata = {
          _id: data._id,
          dataIndex: index + 1,
          id: data.ID,
          productName: data.ProductName,
          productCategory: data.ProductCategories,
          sku: data.SKU,
          barcode: data.Barcode,
          cost: data.CurrentSHMDefaultCost,
          quantity: data.CurrentSHMQuantity,
        };
        productData = [...productData, newdata];
        return productData;
      });
      setWarehouses(productData);
      console.log("warehouse", warehouses);
    }
  };

  useEffect(() => {
    getAllProducts();
  }, [refresh, page, rowsPerPage]);

  return (
    <div
      className="container"
      style={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-evenly",
        alignItems: "center",
      }}
    >
      {/* 1st table */}
      <div>
        <Grid item xs={12} sm={12}>
          <Card style={{ width: "100%", height: "70vh" }}>
            <MuiThemeProvider>
              <MUIDataTable
                style={{ th: { backgroundColor: "grey" } }}
                title="Bulk Delete Variant List"
                data={warehouses}
                columns={columns}
                options={option}
              />
              <TablePagination
                component="div"
                count={totalCount}
                page={page}
                onPageChange={handleChangePage}
                rowsPerPage={rowsPerPage}
                onRowsPerPageChange={handleChangeRowsPerPage}
              />
            </MuiThemeProvider>
          </Card>
        </Grid>
      </div>
      {/* 2nd table */}
      <div>
        <Grid item xs={12} sm={12}>
          <Card style={{ width: "100%", height: "70vh" }}>
            <MuiThemeProvider>
              <MUIDataTable
                style={{ th: { backgroundColor: "grey" } }}
                title="Bulk Delete Variant List"
                data={selectedForDelete}
                columns={columns2}
                options={option2}
              />
              <TablePagination
                component="div"
                count={totalCount2}
                page={page2}
                onPageChange={handleChangePage2}
                rowsPerPage={rowsPerPage2}
                onRowsPerPageChange={handleChangeRowsPerPage2}
              />
            </MuiThemeProvider>
          </Card>
        </Grid>
      </div>
    </div>
  );
};

export default DeleteProducts;
