import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Avatar from "@material-ui/core/Avatar";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemAvatar from "@material-ui/core/ListItemAvatar";
import ListItemText from "@material-ui/core/ListItemText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Dialog from "@material-ui/core/Dialog";
import PersonIcon from "@material-ui/icons/Person";
import AddIcon from "@material-ui/icons/Add";
import Typography from "@material-ui/core/Typography";
import { blue } from "@material-ui/core/colors";
import { Form, FormikProvider, useFormik } from "formik";
import axios from "axios";
import * as Yup from "yup";
import { useSnackbar } from "notistack";
import {
  Box,
  Card,
  Container,
  IconButton,
  Menu,
  MenuItem,
  TextField,
  createMuiTheme,
  MuiThemeProvider,
  useTheme,
  Grid,
  DialogContent,
  TablePagination,
} from "@material-ui/core";

const emails = ["username@gmail.com", "user02@gmail.com"];
const useStyles = makeStyles({
  avatar: {
    backgroundColor: blue[100],
    color: blue[600],
  },
});

export default function Addwarehousedialogue(props) {
  const classes = useStyles();
  const { onClose, selectedValue, open } = props;
  const enqueueSnackbar = useSnackbar();

  const handleClose = () => {
    onClose(selectedValue);
  };

  const handleListItemClick = (value) => {
    onClose(value);
  };

  const warehouseSchema = Yup.object().shape({
    location: Yup.string("String").required("Required"),
  });

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      location: "",
    },
    validationSchema: warehouseSchema,

    onSubmit: (values, { setErrors, setSubmitting, resetForm }) => {
      //   console.log(values);
      axios({
        method: "post",
        // url: routes.api.warehouses.addNewWarehouse.url,
        data: values,
      }).then((response) => {
        if (response) {
          if (response.data.success) {
            enqueueSnackbar("Added Successfully", { variant: "success" });
            setSubmitting(false);
            resetForm();
            // handleCloseModal();
          }
        }
      });
    },
  });

  const {
    values,
    errors,
    touched,
    isSubmitting,
    handleSubmit,
    getFieldProps,
    setFieldValue,
  } = formik;

  return (
    <Dialog
      onClose={handleClose}
      aria-labelledby="simple-dialog-title"
      open={open}
    >
      <DialogTitle>Add Warehouse</DialogTitle>
      <DialogContent>
        <FormikProvider value={formik}>
          <Form autoComplete="off">
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6}>
                <TextField
                  // select
                  fullWidth
                  label="Warehouse Location"
                  // inputProps={{ type: 'number' }}
                  {...getFieldProps("location")}
                  error={Boolean(touched.location && errors.location)}
                  helperText={touched.location && errors.location}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <Box
                  sx={{
                    mt: 1,
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  <Button
                    //   onClick={handleCloseModal}
                    variant="contained"
                    color="secondary"
                    sx={{ mr: 1 }}
                  >
                    Cancel
                  </Button>
                  <Button
                    type="submit"
                    variant="contained"
                    pending={isSubmitting}
                    onClick={(e) => {
                      e.preventDefault();
                      // handleSubmit();
                    }}
                  >
                    Save
                  </Button>
                </Box>
              </Grid>
            </Grid>
          </Form>
        </FormikProvider>
      </DialogContent>
    </Dialog>
  );
}

Addwarehousedialogue.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  selectedValue: PropTypes.string.isRequired,
};

