import React from "react";
import { filter } from "lodash";
import MUIDataTable from "mui-datatables";
import { Icon } from "@iconify/react";
import plusFill from "@iconify/icons-eva/plus-fill";
import { Form, FormikProvider, useFormik } from "formik";
import { sentenceCase } from "change-case";
import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import moreVerticalFill from "@iconify/icons-eva/more-vertical-fill";
import Dialog from "@material-ui/core/Dialog";
// material
// import {createMuiTheme, MuiThemeProvider} from '@material-ui/core/styles';
import {
  Box,
  Card,
  Container,
  IconButton,
  Button,
  Menu,
  MenuItem,
  TextField,
  createMuiTheme,
  MuiThemeProvider,
  useTheme,
  Grid,
  DialogTitle,
  DialogContent,
  TablePagination,
  Typography,
  Modal,
} from "@material-ui/core";
import { LoadingButton } from "@material-ui/lab";
// import { useSnackbar } from "notistack";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Yup from "yup";
import routes from "../../routes/routes";
import QRCodeDialogBox from "./QRCodeDialogBox";
// import VehicleDialogbox from './VehicleDialogBox';

// ----------------------------------------------------------------------
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

const options = ["Edit", "Delete", "Change Password"];

const ITEM_HEIGHT = 48;

export default function RacksList() {
  const theme = useTheme();
  // const enqueueSnackbar = useSnackbar();
  //   const { userList } = useSelector((state) => state.users);

  // this state holds the racks data created in the useEffect....

  const [racks, setRacks] = useState([]);
  // o stands for orignal data for the racks.....
  const [oracks, setORacks] = useState([]);
  const [warehouses, setWarehouses] = useState([]);
  const [editItem, setEditItem] = useState({});
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [isQRModal, setIsQRModal] = useState(false);
  const [userId, setUserId] = useState("");
  const [refresh, setRefresh] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [totalCount, setTotalCount] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [page, setPage] = useState(0);
  const [modalOpen, setModalOpen] = React.useState(false);

  const handleModalOpen = () => setModalOpen(true);
  const handleModalClose = () => setModalOpen(false);
  
  const open = Boolean(anchorEl);

  let racksData = [];

  const EditClick = (event, _id, tableMeta) => {
    const data = racks.at(tableMeta.rowIndex);
    const rowdata = oracks.find((item) => item._id === data._id);

    setAnchorEl(event.currentTarget);
    setEditItem(rowdata);
    // console.log(email);
    setIsQRModal(true);
    handleModalOpen()
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    // setPage(0);
  };

  const handleCloseModal = () => {
    // console.log('check');
    setIsQRModal(false);
    setIsOpenModal(false);
    setRefresh(!refresh);
  };

  const handleModal = () => {
    // console.log('check');
    setIsOpenModal(true);
    // setRefresh(!refresh);
  };

  const getMuiTheme = () => createMuiTheme({});

  const columns = [
    {
      name: "w_no",
      label: "Warehoue No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `W${value}`,
      },
    },
    {
      name: "no",
      label: "Rack No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `R${value}`,
      },
    },
    {
      name: "_id",
      label: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => (
          <Button
            style={{ backgroundColor: "lightgray", textAlign: "start" }}
            onClick={(e) => EditClick(e, value, tableMeta)}
          >
            QR Code
          </Button>
        ),
      },
    },
  ];

  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
  };

  const getAllRacks = async () => {
    console.log("hello");
    const wData = axios({
      method: "get",
      url: routes.api.warehouses.getAllWarehouses.url,
    });

    const rData = axios({
      method: "get",
      url: routes.api.racks.getAllRacks.url,
      params: { limit: rowsPerPage, page: page },
    });

    const [warehouse, rack] = await axios.all([wData, rData]);

    if (warehouse.data.success) {
      setWarehouses(warehouse.data.warehouses);
      console.log("warehousedata", warehouse.data.warehouses);
    }

    if (rack.data.success) {
      console.log("rackdata", rack.data);

      setTotalCount(rack.data.totalDoc);

      rack.data.racks.map((data) => {
        const newdata = {
          _id: data._id,
          no: data.no,
          w_no: data.warehouse_id.no,
        };
        racksData = [...racksData, newdata];
        return racksData;
      });
      setRacks(racksData);
      setORacks(rack.data.racks);
    }
  };

  useEffect(() => {
    getAllRacks();
  }, [refresh, page, rowsPerPage]);

  const notify = () => {
    console.log("toast");
    toast.success("Added Successfully!", {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: true,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };

  const rackSchema = Yup.object().shape({
    warehouse_id: Yup.string().required("Required"),
    noOfRacks: Yup.number()
      .required("Required")
      .positive("Should be Positive")
      .integer(),
  });

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      noOfRacks: 1,
      warehouse_id: "",
    },
    validationSchema: rackSchema,

    onSubmit: (values, { setErrors, setSubmitting, resetForm }) => {
      //   console.log(values);

      axios({
        method: "post",
        url: routes.api.racks.addNewRack.url,
        data: values,
      }).then((response) => {
        if (response) {
          if (response.data.success) {
            // enqueueSnackbar("Added Successfully", { variant: "success" });
            notify()
            setSubmitting(false);
            resetForm();
            handleCloseModal();
          }
        }
      });
    },
  });
  
  const {
    values,
    errors,
    touched,
    isSubmitting,
    handleSubmit,
    getFieldProps,
    setFieldValue,
  } = formik;

  return (
    <div>
      <Container maxWidth="xl" style={{ padding: "0px" }}>
      <ToastContainer />
        <Grid style={{ display: "flex", flexDirection: "column" }}>
          <Grid
            item
            xs={12}
            sm={12}
            style={{ marginBottom: "5px", textAlign: "right" }}
          >
            <Button
              variant="contained"
              startIcon={<Icon icon={plusFill} width={20} height={20} />}
              onClick={handleModal}
              className="primaryButton"
            >
              New Rack
            </Button>
          </Grid>
          <Grid item xs={12} sm={12}>
            <Card>
              <MuiThemeProvider theme={theme}>
                <MUIDataTable
                  style={{ th: { backgroundColor: "grey" } }}
                  //   title="Employees List"
                  data={racks}
                  columns={columns}
                  options={option}
                />
                <TablePagination
                  component="div"
                  count={totalCount}
                  page={page}
                  onPageChange={handleChangePage}
                  rowsPerPage={rowsPerPage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </MuiThemeProvider>
            </Card>
          </Grid>
        </Grid>
        {isQRModal ? (
          <Modal
          open={modalOpen}
          onClose={handleModalClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
          <QRCodeDialogBox
            isQRModal={isQRModal}
            isType="warehouse_id"
            
            editItem={editItem}
            onClose={handleCloseModal}
            />
            </Box>
          </Modal>
        ) : null}
        {isOpenModal ? (
          <Dialog open={isOpenModal} onClose={handleCloseModal}>
            <DialogTitle>Add Racks</DialogTitle>
            <DialogContent>
              <FormikProvider value={formik}>
                <Form autoComplete="off">
                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        select
                        fullWidth
                        label="Warehouse"
                        {...getFieldProps("warehouse_id")}
                        SelectProps={{ native: true }}
                      >
                        <option value="" />
                        {warehouses.map((option) => (
                          <option key={`${option._id}`} value={option._id}>
                            W{option.no},{option.location}
                          </option>
                        ))}
                      </TextField>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        // select
                        fullWidth
                        label="No of Racks"
                        inputProps={{ type: "number" }}
                        {...getFieldProps("noOfRacks")}
                        error={Boolean(touched.noOfRacks && errors.noOfRacks)}
                        helperText={touched.noOfRacks && errors.noOfRacks}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <Box
                        sx={{
                          mt: 1,
                          display: "flex",
                          justifyContent: "center",
                        }}
                      >
                        <Button
                          onClick={handleCloseModal}
                          variant="contained"
                          color="secondary"
                          sx={{ mr: 1 }}
                        >
                          Cancel
                        </Button>
                        <Button
                          type="submit"
                          variant="contained"
                          pending={isSubmitting}
                          onClick={(e) => {
                            e.preventDefault();
                            handleSubmit();
                          }}
                        >
                          Save
                        </Button>
                      </Box>
                    </Grid>
                  </Grid>
                </Form>
              </FormikProvider>
            </DialogContent>
          </Dialog>
        ) : null}
      </Container>
    </div>
  );
}
